.container{
  margin: 0;
  padding: 0;
  min-height: 800px;
}

.cont-dev {
  display:inline;
}

.cont-web{
  background-color: #005;
  opacity: 60%;
  display: none;
  width: 800px;
}
.cont-web img{
  width: 90%;
  margin: 0 auto 0 auto;
}
.left{
  float: left;
  width:50%;
  padding-top: 20px;
}
.right{
  float: right;
  width:50%;
}
  .cont-web-new{
  background-color: #005;
  opacity: 100%;
  width: 750px;
  }

@media screen and (min-width:480px) {
  .cont-dev {
    display: none;
  }
  .cont-web{
    margin: 0 auto 0 auto;
    padding-top: 10px;
    background-color: rgba(255,255,255,0.9);
    border-radius: 10px;
    width: 750px;
    bottom: auto;
    display: inherit;
    min-height: 800px;
  }
}

ul.cont{
  list-style-type: circle;
  margin: 0;
  padding: 0;

}
.cont > li{
float: none;
}

.button {
    background-color: #005;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 5px;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    -webkit-box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

.button:hover {
    background-color: #000;
    color: white;
}

a.mail{
  color:blue;
  text-decoration: underline;
  margin-left: 0;
  padding-left: 0;
  text-align: left;
}
a.mail:hover{
  color:black;
  background-color: inherit;
}



